<script setup>
import { onMounted, onUnmounted, ref } from "vue"
import Values from "./ValuesContent.vue"
import Team from "./TeamContent.vue"
import Description from "./DescriptionContent.vue"
import MobileTeam from "./MobileTeamContent.vue"

const description = ref(null);
const values = ref(null);
const team = ref(null)

const refs = {
    description,
    values,
    team
}

const scrollTo = (refName) => {
    const element = refs[refName];
    element.value.scrollIntoView();
}

const elements = document.getElementsByClassName("fade");
const buttons = document.getElementsByName("section");

const fadeIn = () => {
    for (let [index, element] of Array.from(elements).entries()) {
        const scrollBottom = window.pageYOffset + window.innerHeight;
        const offset = element.offsetTop;
        const opacity = ((scrollBottom - offset) / (window.innerHeight / 2)) ** 3;
        element.style.opacity = opacity;
        if (opacity >= 1) {
            buttons[index].checked = true;
        }
        else {
            buttons[index].checked = false;
        }
    }
}

onMounted(() => {
    window.addEventListener('scroll', fadeIn)
})

onUnmounted(() => {
    window.removeEventListener('scroll', fadeIn);
})
</script>

<template>
    <div class="h-auto w-[100vw]">
        <aside class="hidden fixed md:flex flex-col items-center justify-center gap-4 pl-10 top-0 h-[100vh]">
            <input @click="scrollTo('description')" type="radio" id="descriptionButton" name="section" checked/>
            <label for="descriptionButton">&#8203;</label>
            <input @click="scrollTo('values')" type="radio" id="valuesButton" name="section"/>
            <label for="valuesButton">&#8203;</label>
            <input @click="scrollTo('team')" type="radio" id="teamButton" name="section"/>
            <label for="teamButton">&#8203;</label>
        </aside>
        <div class="h-auto w-full">
        <div class="flex flex-col h-auto w-full">
        <div class="h-auto flex flex-col w-[100vw] items-start justify-start md:w-[65vw] px-6 scroll-smooth md:pl-32">
            <section ref="description" class="min-h-[100vh] w-full flex fade pb-[10vh]"><Description/></section>
            <section ref="values" class="min-h-[100vh] w-full flex mb-[10vh]"><Values/></section>
            <div ref="team">
                <section class="hidden min-h-[100vh] w-full md:flex"><Team/></section>
                <section class="min-h-[100vh] w-[100vw] flex -ml-6 md:md:w-[65vw] md:hidden"><MobileTeam/></section>
            </div>
        </div>
        </div>
